import { getOriginRegex } from './base'
import * as types from '@babel/types'
import { baseUtils } from '.'

// todo è¿™ä¸ªåˆ‡å‰²å‡½æ•°å¯ä»¥ä¼˜åŒ–ï¼Œæ€§èƒ½å¯èƒ½å¾ˆå·®
/**
 * æ ¹æ®æ­£åˆ™è¡¨è¾¾å¼åˆ†å‰²å­—ç¬¦ä¸²ï¼Œå¹¶å°†ç¬¦åˆæ­£åˆ™çš„è¿ç»­å­—ç¬¦æ‹¼æ¥èµ·æ¥ã€‚
 * @param str - è¦åˆ†å‰²çš„å­—ç¬¦ä¸²ã€‚
 * @param separatorRegex - ç”¨äºåˆ†å‰²å­—ç¬¦ä¸²çš„æ­£åˆ™è¡¨è¾¾å¼ã€‚
 * @returns åˆ†å‰²å¹¶æ‹¼æ¥åçš„å­—ç¬¦ä¸²æ•°ç»„ã€‚
 */
export function splitByRegex(str: string, separatorRegex: RegExp): string[] {
    // å®šä¹‰æ ‡ç‚¹ç¬¦å·çš„æ­£åˆ™è¡¨è¾¾å¼
    const punctuationRegex = /[ï¼Œã€‚ï¼Ÿï¼ã€Šã€‹,.ï¼ï¼š!?""''ï¼›â€˜â€œã€0-9]/
    // åˆ›å»ºä¸€ä¸ªæ–°çš„æ­£åˆ™è¡¨è¾¾å¼ï¼Œç”¨äºåˆ†å‰²å­—ç¬¦ä¸²
    const splitRegex = new RegExp(
        `(${separatorRegex.source}|${punctuationRegex.source})`,
        separatorRegex.flags
    )

    // ä½¿ç”¨æ­£åˆ™è¡¨è¾¾å¼åˆ†å‰²å­—ç¬¦ä¸²ï¼Œå¹¶è¿‡æ»¤æ‰ç©ºå­—ç¬¦ä¸²
    const splitArr = str.split(splitRegex).filter(Boolean)
    const result: string[] = []
    let currentMatch = ''

    // å®šä¹‰è¿æ¥æ ‡ç‚¹ç¬¦å·çš„æ­£åˆ™è¡¨è¾¾å¼
    const connectPunctuationRegex = /[ï¼Œã€‚ï¼Ÿï¼ã€Šã€‹,.ï¼ï¼š!?ï¼›â€˜â€œã€0-9]/
    // åˆ›å»ºä¸€ä¸ªæ–°çš„æ­£åˆ™è¡¨è¾¾å¼ï¼Œç”¨äºæ£€æµ‹æ˜¯å¦éœ€è¦è¿æ¥
    const connectRegex = new RegExp(
        `(${separatorRegex.source}|${connectPunctuationRegex.source})`,
        separatorRegex.flags
    )

    // éå†åˆ†å‰²åçš„æ•°ç»„
    for (const item of splitArr) {
        if (connectRegex.test(item)) {
            // å¦‚æœå½“å‰é¡¹ç¬¦åˆè¿æ¥æ¡ä»¶ï¼Œåˆ™å°†å…¶æ·»åŠ åˆ°å½“å‰åŒ¹é…å­—ç¬¦ä¸²ä¸­
            currentMatch += item
        } else {
            // å¦‚æœå½“å‰åŒ¹é…å­—ç¬¦ä¸²ä¸ä¸ºç©ºï¼Œåˆ™å°†å…¶æ·»åŠ åˆ°ç»“æœæ•°ç»„ä¸­
            if (currentMatch) {
                result.push(currentMatch)
                currentMatch = ''
            }
            // å°†å½“å‰é¡¹æ·»åŠ åˆ°ç»“æœæ•°ç»„ä¸­
            result.push(item)
        }
    }

    // å¦‚æœæœ€åä¸€ä¸ªåŒ¹é…å­—ç¬¦ä¸²ä¸ä¸ºç©ºï¼Œåˆ™å°†å…¶æ·»åŠ åˆ°ç»“æœæ•°ç»„ä¸­
    if (currentMatch) {
        result.push(currentMatch)
    }

    // å†éå†ä¸€æ¬¡ï¼ŒæŠŠä¸ç¬¦åˆseparatorRegex è¿™ä¸ªæ­£åˆ™çš„æ‹¼èµ·æ¥
    const finalResult: string[] = []
    let tempStr = ''

    for (let i = 0; i < result.length; i++) {
        const item = result[i]
        if (separatorRegex.test(item)) {
            if (tempStr) {
                finalResult.push(tempStr)
                tempStr = ''
            }
            finalResult.push(item)
        } else {
            tempStr += item
            if (i === result.length - 1 || separatorRegex.test(result[i + 1])) {
                finalResult.push(tempStr)
                tempStr = ''
            }
        }
    }

    if (tempStr) {
        finalResult.push(tempStr)
    }
    return finalResult
}

/**
 * æ£€æŸ¥å­—ç¬¦ä¸²æ˜¯å¦éœ€è¦åˆ‡å‰²ã€‚
 * @param str - è¦æ£€æŸ¥çš„å­—ç¬¦ä¸²ã€‚
 * @returns å¦‚æœå­—ç¬¦ä¸²éœ€è¦åˆ‡å‰²ï¼Œåˆ™è¿”å› trueï¼Œå¦åˆ™è¿”å› falseã€‚
 */
export function checkNeedSplit(str: string) {
    // æ£€æŸ¥å­—ç¬¦ä¸²ä¸­æ˜¯å¦åŒ…å«éœ€è¦åˆ‡å‰²çš„ç‰¹æ®Šå­—ç¬¦
    return (
        str.includes('\n') ||
        str.includes('\\') ||
        str.includes('\r') ||
        str.includes('\t') ||
        str.includes('\v') ||
        str.includes('\f') ||
        str.includes('>') ||
        str.includes('<')
    )
}

/**
 * @description: å°†å­—ç¬¦ä¸²æ•°ç»„è½¬æ¢ä¸ºbabelçš„æ¨¡æ¿å­—ç¬¦ä¸²èŠ‚ç‚¹
 * @param {string[]} strArray - å­—ç¬¦ä¸²æ•°ç»„
 * @return {types.CallExpression} - babelçš„æ·±åº¦æ‰«æçš„è¡¨è¾¾å¼
 */
export function convertToTemplateLiteral(strArray: string[], option?: any): types.CallExpression {
    const quasis: types.TemplateElement[] = []
    const expressions: types.Expression[] = []

    strArray.forEach((str, index) => {
        if (index === 0) {
            if (getOriginRegex().test(str)) {
                quasis.push(types.templateElement({ raw: '', cooked: '' }, false))
                expressions.push(
                    baseUtils.createI18nTranslator({
                        value: str,
                        isExpression: true,
                        insertOption: option
                    })
                )
            } else {
                quasis.push(types.templateElement({ raw: str, cooked: str }, false))
            }
        } else {
            if (getOriginRegex().test(str)) {
                expressions.push(
                    baseUtils.createI18nTranslator({
                        value: str,
                        isExpression: true,
                        insertOption: option
                    })
                )
            } else {
                quasis.push(types.templateElement({ raw: str, cooked: str }, false))
            }
        }
    })

    if (quasis.length === expressions.length) {
        quasis.push(types.templateElement({ raw: '', cooked: '' }, true))
    } else if (quasis.length > expressions.length) {
        quasis[quasis.length - 1].tail = true
    }

    const templateLiteral = types.templateLiteral(quasis, expressions)
    const deepScanCall = types.callExpression(types.identifier('$deepScan'), [templateLiteral])
    // æ‰“å°è½¬æ¢ç»“æœ
    // console.log('deepScanCall', (generate as any).default(deepScanCall).code)
    return deepScanCall
}
