import { IntervalQueue } from './IntervalQueue'

export interface TranslatorOption {
    /** Translatorç‰ˆæœ¬ï¼Œç”¨äºåšåç»­çš„åŠŸèƒ½è¿­ä»£ */
    version?: number
    /**
     * å®é™…çš„è¯·æ±‚æ–¹æ³•
     * @param text è¢«ç¿»è¯‘çš„æ–‡æœ¬
     * @param fromKey æºè¯­è¨€
     * @param toKey ç›®æ ‡è¯­è¨€
     * @param separator åˆ†éš”ç¬¦
     * @returns ç¿»è¯‘åçš„æ–‡æœ¬
     */
    fetchMethod: (
        text: string,
        fromKey: string,
        toKey: string,
        separator: string
    ) => Promise<string>
    name: string
    /** å•æ¬¡æœ€å¤§ç¿»è¯‘æ–‡æœ¬é•¿åº¦ */
    maxChunkSize?: number
    /** æ‰§è¡Œé—´éš”ï¼ˆé»˜è®¤ä¸å¼€å¯ï¼‰ */
    interval?: number
    /**
     * é”™è¯¯å¤„ç†å‡½æ•°ï¼Œä¸»è¦æ˜¯æ‰“å°æç¤º
     * @param err æŠ›å‡ºçš„å¼‚å¸¸
     * @param defaultErrorHandler é»˜è®¤çš„é”™è¯¯å¤„ç†å‡½æ•°
     * @returns å¦‚æœåœ¨è¿™é‡ŒæŠ›å‡ºå¼‚å¸¸ä¼šä¸­æ–­ç¿»è¯‘
     */
    onError?: (err: unknown, defaultErrorHandler: (error: unknown) => void) => void
}

export class Translator {
    public option: Required<TranslatorOption>

    constructor(option: TranslatorOption) {
        this.option = this.getResultOption(option)
    }

    private defaultErrorHandler = (error: unknown) => {
        const name = this.option.name
        console.error(`ç¿»è¯‘api${name ? `ã€${name}ã€‘` : ''}è¯·æ±‚å¼‚å¸¸ï¼š${this.getErrorMessage(error)}`)
    }

    private getResultOption(option: TranslatorOption) {
        const resultOption: Required<TranslatorOption> = {
            version: 1,
            maxChunkSize: 4500, // ç›®å‰é»˜è®¤æ˜¯4500
            interval: 0,
            onError: this.defaultErrorHandler,
            ...option
        }
        if (resultOption.interval) {
            const getIntervalFn = <T extends unknown[], U extends unknown>(
                fn: (...args: T) => U,
                delay: number
            ) => {
                const queue = new IntervalQueue(fn.bind(null), delay)
                return (...args: T) => {
                    return queue.execute(...args)
                }
            }
            resultOption.fetchMethod = getIntervalFn(
                resultOption.fetchMethod,
                resultOption.interval
            )
        }
        return resultOption
    }

    protected getErrorMessage(error: unknown) {
        if (error instanceof Error) {
            return error.message
        } else {
            return String(error)
        }
    }

    async translate(text: string, fromKey: string, toKey: string, separator: string) {
        let result = ''
        try {
            result = await this.option.fetchMethod(text, fromKey, toKey, separator)
        } catch (error) {
            this.option.onError(error, this.defaultErrorHandler)
        }
        return result
    }
}
