import { baseUtils, translateUtils } from 'src/utils'
import { TranslateTypeEnum } from 'src/enums'
import * as types from '@babel/types'
import { option } from 'src/option'

// æ”¶é›†ç¿»è¯‘å¯¹è±¡
export default function (insertOption: any) {
    return function (path: any) {
        let { node } = path
        // æå–å…¬å…±éƒ¨åˆ†ï¼Œå‡å°‘é‡å¤è®¿é—® node.callee å±æ€§
        const callee = node.callee
        if (
            callee.name === option.translateKey ||
            (callee.property && callee.property.name === option.translateKey) // æ‹“å±• åŠè‡ªåŠ¨æ¨¡å¼ä¸‹çš„ å¦‚ a.b.c() è°ƒç”¨
        ) {
            if (option.translateType === TranslateTypeEnum.SEMI_AUTO) {
                // è·å–å½“å‰ç¿»è¯‘å‡½æ•°çš„å‚æ•°
                let arg = node.arguments || []
                // å¦‚æœå‚æ•°æ•°é‡ä¸ä¸º 1ï¼Œåˆ™ç›´æ¥è¿”å›
                if (arg.length === 1) {
                    const value = arg[0]?.value || ''
                    // ç”ŸæˆçœŸå®è°ƒç”¨å‡½æ•°
                    const replaceNode = baseUtils.createI18nTranslator({
                        insertOption,
                        value,
                        isExpression: true
                    })
                    path.replaceWith(replaceNode)
                    translateSetLang(replaceNode)
                }
            } else if (option.translateType === TranslateTypeEnum.FULL_AUTO) {
                // å…¨è‡ªåŠ¨æ¨¡å¼ä¸‹è¿˜æ˜¯åªæ”¶é›† å•ç‹¬ $t è°ƒç”¨
                if (
                    callee.name === option.translateKey ||
                    (callee.property && callee.property.name === option.translateKey)
                )
                    translateSetLang(node)
            }
        }
    }
}

/**
 * @description: å¤„ç†ç¿»è¯‘å¹¶è®¾ç½®è¯­è¨€å¯¹è±¡å±æ€§
 * @param node - è°ƒç”¨è¡¨è¾¾å¼èŠ‚ç‚¹
 * @return
 */
function translateSetLang(node: types.CallExpression) {
    // è·å–è°ƒç”¨è¡¨è¾¾å¼çš„å‚æ•°
    let arg = node.arguments || []
    // æå–å‚æ•°ä½œä¸ºå€¼
    // æ£€æŸ¥å‚æ•°æ˜¯å¦ä¸ºå­—ç¬¦ä¸²å­—é¢é‡
    const id = types.isStringLiteral(arg[0]) ? arg[0].value : ''
    const value = types.isStringLiteral(arg[1]) ? arg[1].value : ''
    // æ£€æŸ¥ ID å’Œå€¼æ˜¯å¦å­˜åœ¨ï¼Œå¹¶ä¸”ç¬¬äºŒä¸ªå‚æ•°æ˜¯å­—ç¬¦ä¸²å­—é¢é‡
    if (id && value && types.isStringLiteral(arg[1])) {
        // è°ƒç”¨ç¿»è¯‘å·¥å…·çš„ setLangObj æ–¹æ³•è®¾ç½®è¯­è¨€å¯¹è±¡å±æ€§
        translateUtils.setLangObj(id, value)
    }
}
